@media (max-width: 1000px) {
  * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }

  /* passer body (et tous les éléments de largeur fixe) en largeur automatique */

  body {
    width: auto;
    margin: 0;
    padding: 0;
  }

  /* gestion des mots longs */

  textarea,
  table,
  td,
  th,
  code,
  pre,
  samp {
    -webkit-hyphens: auto; /* césure propre */
    -ms-hyphens: auto;
        hyphens: auto;
    word-wrap: break-word; /* passage à la ligne forcé */
  }

  code,
  pre,
  samp {
    white-space: pre-wrap; /* passage à la ligne spécifique pour les éléments à châsse fixe */
  }

  /* Passer à une seule colonne (à appliquer aux éléments multi-colonnes) */

  .element1,
  .element2 {
    float: none;
    width: auto;
  }

  /* masquer les éléments superflus */

  .hide_mobile {
    display: none !important;
  }

  .texte {
    font-size: larger;
    padding: 0px 15px;
  }

  .texteColor {
    font-size: larger;
  }
  .image1 {
    width: 90vw;
    margin-top: 100px;
    margin-left: -5vw;
  }
  
  .image2 {
    width: 80vw;
    margin-top: 50vh;
    margin-left: 8vw;
  }
  
}

/* ////////  Hover Project vodoo magic */

.container {
  position: absolute;
}

.containerProj {
  position: absolute;
}

.overlayProject {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  opacity: 0;
}

.red {
  background: #e6084be7;
  margin-bottom: 5px;
}

.bleu {
  background: #4100dae7;
  margin-bottom: 5px;
}
.container:hover .overlayProject {
  opacity: 1;
}

.container:hover .image {
  mix-blend-mode: screen;
  -webkit-filter: grayscale(100%) contrast(200%);
  filter: grayscale(100%) contrast(200%);
  opacity: 0.75;
}

.textHover {
  color: rgb(250, 250, 250);
  font-size: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.blockScroll {
